.search {
  min-height: 81vh;
}

.searchbar {
  z-index: 1;
  color: #fff;
  background-color: #3f51b5;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding: 12px 24px;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px -1px #0003, 0 4px 5px #00000024, 0 1px 10px #0000001f;
}

.searchForm {
  background-color: #fff;
  border-radius: 3px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  display: flex;
  overflow: hidden;
}

.searchBtn {
  opacity: .6;
  cursor: pointer;
  background-image: url("search-icon.6319a08a.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  border: 0;
  outline: none;
  width: 48px;
  height: 48px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.searchBtn:hover {
  opacity: 1;
}

.searchBtn-label {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.searchInput {
  font: inherit;
  border: none;
  outline: none;
  width: 100%;
  padding-left: 4px;
  padding-right: 4px;
  font-size: 20px;
  display: inline-block;
}

.searchInput::placeholder {
  font: inherit;
  font-size: 18px;
}

.modal {
  z-index: 2;
  background-color: #000000b3;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #f2f2f2;
  border: 1px solid #888;
  width: 70%;
  margin: 15% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

